import { ON_SCROLL, SHOW_SKILL } from '../../actions/scroll_action';

function onScroll (state = 0, action) {
    switch (action.type) {
        case ON_SCROLL:
            return action.pos;
        default:
            return state;
    }
}

function showSkill (state = false, action) {
    switch (action.type) {
        case SHOW_SKILL:
            return action.show;
        default:
            return state;
    }
}

export default scroll = {
    show: showSkill
}



