import { combineReducers } from 'redux';
import Scroll from './reducer_childs/scroll_reducer.js';

let addReducer = (() => {
    let reducers = {};
    return  (oldOne) => {
        if (!oldOne) {
            return reducers;
        } else {
            reducers = {
                ...reducers, ...oldOne
            };
        }
    }
})();

addReducer(Scroll);

const result = combineReducers(addReducer());

export default result;



